import React, { useState } from "react"

import {FaFacebookSquare, FaInstagramSquare, FaLinkedin, FaTwitterSquare} from 'react-icons/fa'

import Layout from "../components/layout"
import SEO from "../components/seo"

import BG from "../assets/img/bg2.jpg"
import ASImg from "../assets/svg/ArtStation-logo-horizontal-dark.svg"





const IndexPage = () => {

  //state show bg and hide other elements
  const [showBg, setShowBg] = useState(false)
  const bgOpacity = {
    opacity: (showBg === true ? `1`: `0`)
  }
  const elementOpacity = {
    opacity: (showBg === true ? `0`: `1`)
  }




return (
  <Layout>
    <SEO title="Home" />
    
    

    <div className="img-bg uk-cover-container" data-uk-height-viewport>
        <img src={BG} alt="" className="main-bg" data-uk-cover style={bgOpacity}></img>
        <div className="uk-overlay uk-position-center">

          <div className="content-container">
            <div><h1 style={elementOpacity}>Hi, I'm Miruna Georgiana</h1></div>
            <div><h3 className="subtitle" style={elementOpacity}>Concept artist and illustrator</h3></div>
            <div className="art-station-section">
              <a className="art-station-link" href="https://www.artstation.com/mirunageorgiana"
                onMouseEnter={() => setShowBg(true)}
                onMouseLeave={() => setShowBg(false)}
                target="_blank" rel="noopener noreferrer"
              >
                <img src={ASImg} alt="art-station"></img>
              </a>
            </div>
            <div className="social-section" style={elementOpacity}>
              <a href="https://www.facebook.com/miruna.georgiana.522" aria-label="facebook" target="_blank" rel="noopener noreferrer"><FaFacebookSquare size="2rem"/></a>
              <a href="https://www.instagram.com/hyleddart/" aria-label="instagram" target="_blank" rel="noopener noreferrer"><FaInstagramSquare size="2rem"/></a>
              <a href="https://twitter.com/GeorgianaMiruna" aria-label="twitter" target="_blank" rel="noopener noreferrer"><FaTwitterSquare size="2rem"/></a>
              <a href="https://ro.linkedin.com/in/miruna-misteanu-7a092a1b8" aria-label="linkedin" target="_blank" rel="noopener noreferrer"><FaLinkedin size="2rem"/></a>
            </div>
            <div className="email-section" style={elementOpacity}>email me at: <a href="mailto:contact@mirunageorgiana.com">contact@mirunageorgiana.com</a></div>
          </div>

        </div>

        
    </div>
    
  </Layout>
)}

export default IndexPage
